import React, {useState} from "react";
import cl from "./Paginator.module.css";
// import cn from 'classnames';


let Paginator = ({totalItemCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {
    let pageCount = Math.ceil(totalItemCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pageCount; i++) {
        pages.push(i);
    }


    let portionCount = Math.ceil(pageCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return <div className={cl.paginator}>
        <div>
        {portionNumber > 1 &&
        <button onClick={() => {
            setPortionNumber(portionNumber - 1)
        }}>prev</button>}
            {pages
                .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                .map(p => <span onClick={() => onPageChanged(p)}
                                className={cl.pageNumber + ' ' +
                                ((currentPage === p)? cl.selectedPage : '')}
                                    // cn({
                                    //     [cl.selectedPage]: currentPage === p
                                    // }, cl.pageNumber)}
                >{p}
            </span>)}
            {portionCount > portionNumber &&
            <button onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>next</button>}
        </div>
    </div>
};

export default Paginator;